import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import { BtnSave, BtnCancel } from 'components/common/button';
import { Row, Card, Form, Input, InputNumber } from 'antd';
import { getPolicyObjectById, updatePolicyObject, createPolicyObject } from 'services/policyObject';

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    label: '',
    decreasePct: 0,
  };
  return { ...initialValueDefault, ...initialValue };
};

@Form.create({ name: 'CandidateStudentFormPage' })
class CandidateStudentFormPage extends Component {
  state = {
    dataId: false,
    initialValue: {},
  };

  componentDidMount = () => {
    const { match } = this.props;
    if (match.params.id) {
      this.setState({ dataId: match.params.id });
      this.getDataById(match.params.id);
    }
  };

  getDataById = async id => {
    const res = await getPolicyObjectById(id);
    const initialValue = handleInitialValue(res.data);
    this.setState({ initialValue });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      this.saveData(values);
    });
  };

  saveData = async values => {
    const { dataId } = this.state;
    try {
      if (!dataId) {
        await createPolicyObject(values);
      } else {
        await updatePolicyObject({ id: dataId, ...values });
      }
      this.props.history.goBack();
    } catch (error) {
      console.log(error);
    }
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    return (
      <Card title="Thêm ứng viên">
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit}>
            <Form.Item label="Tên đối tượng" hasFeedback>
              {getFieldDecorator('label', {
                initialValue: initialValue.label,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item label="Miễn giảm (%)" hasFeedback>
              {getFieldDecorator('decreasePct', {
                initialValue: initialValue.decreasePct,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<InputNumber min={0} max={99} />)}
            </Form.Item>
            <Form.Item label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel onClick={() => this.props.history.goBack()} />
            </Form.Item>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <CandidateStudentFormPage {...props} />
  </Page>
));



